}

.theme-switch {
  -ms-appearance: none;
  appearance: none;
  margin-left: auto;
  position: relative;
}

.theme-switch:before {
  content: "";
  cursor: pointer;
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  padding: 1px;
  transition: all .3s;
  display: block;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.theme-switch:after {
  content: "";
  cursor: pointer;
  width: 18px;
  height: 18px;
  background-image: url("switch-texture.e49cccd4.svg"), linear-gradient(#fff 0%, #f7f7f7 100%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 50%;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.theme-switch:checked:before {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  transition: all .3s;
}

.theme-switch:checked:after {
  transform: translateX(113%);
}

.scroll-up-btn {
  cursor: pointer;
  width: 70px;
  height: 70px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-self: center;
  align-items: center;
  transition: all .3s;
  display: none;
  position: sticky;
  bottom: 30px;
  left: 100%;
}

.scroll-up-btn:hover {
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, .1), 0px 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1));
  transform: scale(1.2);
}

.trash-btn {
  width: 34px;
  height: 34px;
  cursor: pointer;
  background-color: #4f2ee8;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.trash-btn > .trash-icon {
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: #fff;
  transition: all .3s;
  display: inline-block;
}

.trash-btn:hover {
  background-color: #4f2ee8;
  box-shadow: 0 7px 29px rgba(100, 100, 111, .2);
}

.trash-btn:hover .trash-icon {
  stroke: #eac645;
}

.visually-hidden {
